<div class="journalItem {availableNow} {donated ? "donated" : ""} {donateMultiple ? "selecting" : ""}" on:click={onClickJournalItem}>
    {#if donated}
        <img class="donated" src="./img/donated.svg" alt="donated" />
    {:else if leaving}
        <img class="donated" src="./img/leaving.svg" alt="donated" />
    {/if}
    <img class="icon" src={item.img} alt={item.name} />
</div>

{#if open}
    <ItemDetails {item} on:close={closeDetails} />
{/if}

<script>
// Store
import { donatedList } from "../js/store.js";

// Components
import ItemDetails from "./ItemDetails.svelte";

// Props
export var item;
export var donateMultiple;

// Data
var currentDate = new Date();
var open = false;

// Computed
$: availableNow = isAvailable(currentDate, item);
$: donated = $donatedList.includes(item.id);
$: leaving = isLeaving(currentDate, item);

// Functions
function isAvailable(currentDate, item) {
    var month = currentDate.getMonth() + 1;
    
    if (item.monthsAvailable.includes(month)) {
        var hour = currentDate.getHours();
        var { startTime, endTime } = item.time;
        var spansMidnight = endTime < startTime;

        if (spansMidnight) {
            if (hour >= startTime || hour <= endTime) {
                return "availableTime";
            }
        } else {
            if (hour >= startTime && hour < endTime) {
                return "availableTime";
            }
        }

        return "availableMonth";
    } else {
        return "";
    }
}

function isLeaving(currentDate, item) {
    if (!isAvailable(currentDate, item)) return false;

    var month = currentDate.getMonth() + 2;
    if (month === 13) month = 1;

    return !item.monthsAvailable.includes(month);
}

function onClickJournalItem() {
    if (donateMultiple) {
        toggleDonated();
    } else {
        openDetails();
    }
}

function openDetails() {
    open = true;
}

function closeDetails() {
    open = false;
}

function toggleDonated() {
    if ($donatedList.includes(item.id)) {
        var index = $donatedList.indexOf(item.id);
        $donatedList.splice(index, 1);
        $donatedList = $donatedList;
    } else {
        $donatedList = [
            ...$donatedList,
            item.id,
        ]
    }
}
</script>

<style lang="scss">.journalItem {
  position: relative;
  box-shadow: 0 0 0 1px #000000;
  cursor: pointer; }
  .journalItem .icon {
    width: 80%;
    height: 80%;
    display: block;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .journalItem .donated {
    height: 14px;
    width: 14px;
    position: absolute;
    left: 2px;
    top: 2px; }
  .journalItem.selecting:not(.donated) .icon {
    animation: wiggle 750ms linear infinite; }
  .journalItem.availableMonth {
    background-color: #96ffdc; }
  .journalItem.availableTime {
    background-color: #ffb85c; }
    .journalItem.availableTime:not(.donated):not(.selecting) .icon {
      animation: availableTime 3500ms linear infinite; }

@keyframes wiggle {
  0% {
    transform: translate(-50%, -50%) rotate(0deg); }
  40% {
    transform: translate(-50%, -50%) rotate(0deg); }
  60% {
    transform: translate(-50%, -50%) rotate(-10deg); }
  80% {
    transform: translate(-50%, -50%) rotate(10deg); }
  100% {
    transform: translate(-50%, -50%) rotate(0deg); } }

@keyframes availableTime {
  0% {
    transform: translate(-50%, -50%) scale(1) rotate(0deg); }
  80% {
    transform: translate(-50%, -50%) scale(1) rotate(0deg); }
  84% {
    transform: translate(-50%, -50%) scale(1.03125) rotate(20deg); }
  88% {
    transform: translate(-50%, -50%) scale(1.0625) rotate(-20deg); }
  92% {
    transform: translate(-50%, -50%) scale(1.09375) rotate(20deg); }
  96% {
    transform: translate(-50%, -50%) scale(1.125) rotate(0); }
  100% {
    transform: translate(-50%, -50%) scale(1) rotate(0deg); } }

/*# sourceMappingURL=JournalItem.svelte.css.map */</style>