<main id="app" class="{!motionEnabled ? "disableMotion" : ""}">
    <p>You've donated <span>{$donatedList.length}</span> specimens</p>
    <p>Motion is
        <span class="button" on:click={toggleMotionEnabled}>
            {#if motionEnabled}
                enabled
            {:else}
                disabled
            {/if}
        </span>
    </p>
    <button on:click={toggleDonateMultiple}>
        {#if !donateMultiple}
            Start donating
        {:else}
            Stop donating
        {/if}
    </button>
    <div class="mainGrid">
        {#each totalList as item}
            <JournalItem {item} donateMultiple={donateMultiple} />
        {/each}
    </div>
    <!-- <button class="donateMultiple" on:click={toggleDonateMultiple}>
        Donate multiple
    </button> -->
    <!-- <p>Photo by Augustine Wong on Unsplash</p> -->
</main>

<script>
// Stores
import { donatedList } from "./js/store.js";

// Components
import JournalItem from "./components/JournalItem.svelte";

// Data
import fish from "./json/fish.json";
import bugs from "./json/bugs.json";
var donateMultiple = false;
var motionEnabled = true;

// Computed
$: totalList = [
    ...fish.fish,
    ...bugs.bugs,
];

// Functions 
function toggleDonateMultiple() {
    donateMultiple = !donateMultiple;
}

function toggleMotionEnabled() {
    motionEnabled = !motionEnabled;
}
</script>

<style lang="scss">main {
  padding: 32px 0;
  background-color: #fbffe6; }
  main.disableMotion :global(*) {
    animation: none !important; }

p {
  width: 95%;
  max-width: 935px;
  margin: 16px auto;
  text-align: center; }
  p > span {
    font-weight: 700;
    color: #0079b1; }
    p > span.button {
      text-decoration: underline;
      cursor: pointer; }

.mainGrid {
  width: 95%;
  max-width: 935px;
  margin: auto;
  display: grid;
  grid-auto-rows: 64px;
  grid-template-columns: repeat(auto-fit, 64px);
  grid-gap: 1px; }

button {
  display: block;
  margin: 16px auto;
  transform: rotate(2deg);
  background-color: #ffad60;
  color: #000000;
  border: 0;
  box-shadow: 0 0 4px #00000033;
  cursor: pointer; }

/*# sourceMappingURL=App.svelte.css.map */</style>